import { _decorator, Component, Node, Label, Sprite, RichText, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PrefabForecast')

export class PrefabForecast extends Component {

    @property(Label)
    lblToday: Label = null;

    @property(Label)
    lblTomorrow: Label = null;

    @property(Label)
    lblMonth: Label = null;

    @property(Label)
    lblNextMonth: Label = null;

    @property(Sprite)
    imgWeatherDay: Sprite = null;

    @property(Sprite)
    imgWeatherMonth: Sprite = null;

    @property(RichText)
    richTextDay: RichText = null;

    @property(RichText)
    richTextMonth: RichText = null;


    @property(Sprite)
    imgArrowUpDown: Sprite = null;

    @property(Node)
    nodeMonth: Node = null;
    

    @property(SpriteFrame)
    icSun: SpriteFrame | null = null;
    @property(SpriteFrame)
    icMoon: SpriteFrame | null = null;


   

    onLoad(){
    
        this.initLayout()

    }
    start() {

    }


    update(deltaTime: number) {
        
    }


    initLayout(){
        this.hideNodeMonth()


        
    }



    showHideNodeMonth(){

        this.nodeMonth.active ? this.hideNodeMonth() : this.showNodeMonth()
    }



    hideNodeMonth(){
        this.nodeMonth.active=false
        this.imgArrowUpDown.node.angle=0

    // this.imgArrowUpDown.getComponent(Sprite).spriteFrame= AudioManager.getMusic()==="true"? this.icON : this.icOFF

    }

    showNodeMonth(){
        this.nodeMonth.active=true
        this.imgArrowUpDown.node.angle=180


    }
}

